'use client';

import styles from '../styles/ChatBot.module.css';
import { useState } from 'react';

const ChatBot = () => {
  const [message, setMessage] = useState('');
  const [chat, setChat] = useState([]);
  const [loading, setLoading] = useState(false);

  const handleSend = async () => {
    if (!message.trim()) return;
  
    const newChat = [...chat, { role: 'user', content: message }];
    setChat(newChat);
    setMessage('');
    setLoading(true);
  
    try {
      const response = await fetch('/api/chat', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          messages: [
            {
              role: 'system',
              content:
                'You are an expert financial assistant specialized in credit health, FusionScore, and personal finance. Always give helpful, friendly advice related to Unified Credit Score, loans, improving poor credit, applying for financial products, and overcoming financial challenges.',
            },
            ...newChat,
          ],
        }),
      });
  
      const data = await response.json();
      const reply = data?.reply || 'Hmm, I had trouble answering that. Try rephrasing.';
  
      setChat((prev) => [...prev, { role: 'bot', content: reply }]);
    } catch (error) {
      console.error('Chat error:', error);
      setChat((prev) => [
        ...prev,
        { role: 'bot', content: 'Oops! Something went wrong. Please try again.' },
      ]);
    } finally {
      setLoading(false);
    }
  };
  

  const suggestions = [
    'ðŸ’¡ What is FusionScore?',
    'ðŸ’¡ How can I build credit with no history?',
    'ðŸ’¡ I have a poor score. Can I still get a loan?',
    'ðŸ’¡ Which credit bureaus are used?',
    'ðŸ’¡ How can I improve my credit score?',
  ];

  return (
    <div className={styles.chatContainer}>
      <div className={styles.chatBox}>
        {chat.map((msg, index) => (
          <p key={index}>
            <strong>{msg.role === 'user' ? 'You' : 'AI'}:</strong> {msg.content}
          </p>
        ))}
        {loading && <p><strong>AI:</strong> Typing...</p>}
      </div>

      <div className={styles.inputRow}>
        <input
          className={styles.chatInput}
          type="text"
          placeholder="Ask about FusionScore, credit tips, etc."
          value={message}
          onChange={(e) => setMessage(e.target.value)}
          onKeyDown={(e) => e.key === 'Enter' && handleSend()}
        />
        <button className={styles.sendButton} onClick={handleSend}>
          Send
        </button>
      </div>

      <div style={{ marginTop: '20px' }}>
        <strong>Try asking:</strong>
        <ul>
          {suggestions.map((s, i) => (
            <li key={i}>{s}</li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default ChatBot;
